@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Roboto', sans-serif;
  background-color: #fcf6f5ff;
  height: 100vh;
}
.hero {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  padding: 0 3rem;
}

h1,
h2 {
  font-weight: 200;
  margin: 0.4rem;
}

h2 {
  font-size: 2rem;
}

.txt-type > .txt {
  border-right: 0.2rem solid #777;
}
.navigation {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  color: white;
  padding: 0 25px;
}

.navigation ul {
  display: flex;
  justify-content: flex-end;
  list-style: none;
}
.navigation ul li {
  margin: 0 10px;
  border: 1px solid;
  padding: 10px;
  border-radius: 8px;
  color: white;
}

.navigation ul li a {
  text-decoration: none;
  color: white;
}

.navigation ul li:hover {
  background-color: white;
}
.navigation ul li a:hover {
  color: black;
}
@media (min-width: 1200px) {
  h1 {
    font-size: 5rem;
  }
}

@media (max-width: 800px) {
  .hero {
    padding: 0 1rem;
  }
  h1 {
    font-size: 3rem;
  }
}

@media (max-width: 500px) {
  h1 {
    font-size: 2.5rem;
  }
  h2 {
    font-size: 1.5rem;
  }
}

body {
  background: white;
  /* background-image: radial-gradient(#a8d4a2b0 20%, transparent 0),
    radial-gradient(#ffffff 20%, transparent 0);
  background-size: 30px 30px; */
  background-position: 0 0, 15px 15px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

header {
  background: linear-gradient(
      0deg,
      rgba(97, 93, 108, 0.9),
      rgba(97, 93, 108, 0.1)
    ),
    url(/portfolio/static/media/Header.e2b64c22.jpg) center;
  background-size: cover;
  position: relative;
  color: white;
}

.profile img {
  height: 150px;
  width: 150px;
  border-radius: 50%;
}

.hero {
  padding: 30px;
  color: white;
  text-align: center;
  height: 300px;
  position: relative;
}

.hero h1 {
  font-size: 50px;
}

.go-up-button {
  position: fixed;
  right: 30px;
  bottom: 20px;
}

main {
  text-align: center;
  margin-top: 100px;
}
h2 {
  text-align: center;
  text-decoration: underline;
}

.project {
  display: flex;
  background: white;
  max-width: 90%;
  margin: 30px auto;
  border: 1px solid black;
  border-radius: 8px;
  min-height: 500px;
  overflow: hidden;
  border-bottom: 1px solid black;
  text-align: center;
  flex-wrap: wrap;
}

.project:hover {
  -webkit-transform: translate3d(0, -5px, 0);
          transform: translate3d(0, -5px, 0);
  box-shadow: 0 2rem 5rem 0 rgba(0, 0, 0, 0.1);
  transition: all 0.25s ease;
}
.project-description {
  width: 60%;
}

.project-description p {
  font-size: 23px;
  padding: 15px;
}
.project-picture {
  width: 40%;
  display: flex;
  align-items: center;
}

.project img {
  width: 80%;
  height: 400px;
}

.project ul {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 54%;
  margin: 0 auto;
}
.project ul li {
  padding: 5px 8px;
  border-radius: 3px;
  margin: 5px;
  background-color: #4c5357;
  color: white;
}

.project-title {
  font-size: 2.5rem;
}

.description-content {
  margin-top: 50px;
}

.links {
  width: 75%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  margin: 10px auto;
}

.links a {
  text-decoration: none;
  color: #797878;
  margin: 0 20px;
  font-size: 20px;
  padding: 10px 0;
  position: relative;
}
.links a:active,
.links a:hover {
  text-decoration: underline;
  transition: all 0.2s ease-in-out;
  transition-duration: 1s;
}
.demo,
.source {
  margin: 10px;
}

@media screen and(min-width: 1100px) {
  .project {
    width: 40%;
  }
}

#about-me img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}

#about-me {
  width: 55%;
  margin: 0 auto;
  background-color: white;
  font-size: 25px;
  line-height: 1.6;
}

#about-me .text {
  margin: 20px 0;
}

.technologies-div {
  font-size: 60px;
}

.contact {
  text-decoration: none;
}

#work-together {
  font-size: 20px;
  width: 55%;
  margin: 0 auto;
}

footer {
  margin-top: 50px;
  padding: 20px 0;
}
.footer-icons {
  font-size: 20px;
  width: 15%;
  margin-left: 20px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.footer-icons a {
  text-decoration: none;
  color: black;
}

footer {
  display: flex;
}

.footer-text {
  display: flex;
  justify-content: flex-end;
  flex-grow: 1;
  margin-right: 30px;
}

.heart {
  color: darkred;
}

